@import "../../App/сolors";
@import "../../App/mixins";


%no-list-style {
	list-style: none;
	margin: 0;
	padding: 0;
}
%flex-props {
	flex-basis: 0;
	flex-grow: 1;
}

.dropdown {
	display: flex;
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	background-color: rgba(21, 27, 31, 0.8);
	font: 500 32px "Roboto", "Arial", sans-serif;

	transform: translateX(-100%);
	transition: all 0.3s ease-out;

	.dropdown__sidebar {
		min-width: 64px;
		background-color: #111518;

		.dropdown__lang-button {
			display: none;
		}
	}
	.dropdown__opaque {
		@extend %flex-props;
		min-width: 550px;
		background-color: #111518;

		.dropdown__content {
			padding-left: 64px;

			.dropdown__links {
				@extend %no-list-style;
				margin-top: calc(50vh - 100px);

				li {
					margin-bottom: 15px;
				}
				.dropdown__link {
					text-transform: uppercase;
					text-decoration: none;

					&_white {
						@include link(white, $accent, $accent);
					}
				}
			}
			.dropdown__social {
				@extend %no-list-style;
				margin-top: 38px;
				display: flex;

				li {
					margin-right: 24px;

					svg {
						display: block;
						&:hover {
							path {
								fill: $accent;
							}
						}
					}
				}
			}
		}
	}
	.dropdown__transparent {
		@extend %flex-props;
	}

	&_active {
		transform: translateX(0);
	}
}

@import "./medias";
